/* roboto-condensed-200 - cyrillic */
@font-face {
  font-display: swap;
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 200;
  src:
    url("../fonts/roboto-condensed-v27-cyrillic-200.woff2") format("woff2"),
    url("../fonts/roboto-condensed-v27-cyrillic-200.woff") format("woff");
}

