html,
body {
  display: grid;
  min-block-size: 100%;
  margin: 0;
  padding: 0;
}

html {
  font-family: "Roboto", " Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 137%;
  color: #192144;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  scroll-behavior: smooth;
}

a {
  color: #000000;
  text-decoration: none;
  -webkit-tap-highlight-color: transparent;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
  -webkit-tap-highlight-color: transparent;
}

.wrapper {
  display: grid;
  min-block-size: 100%;
  grid-template-rows: auto 1fr;
}
.wrapper--catalog {
  background-color: #edeff6;
}

.rouble {
  /* stylelint-disable */
  font-weight: 400 !important;
  /* stylelint-enable */
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: fixed;
  transform: scale(0);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.tippy-box {
  padding: 18px 14px;
  font-family: "Roboto", " Arial", sans-serif;
  font-weight: 400;
  font-size: 20px;
  line-height: 100%;
  text-align: center;
  color: #ffffff;
  background-color: #101d41;
}
.tippy-box > .tippy-arrow {
  color: #101d41;
}

.container {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 60px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 40px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 25px;
  }
}

.header {
  position: relative;
  padding: 46px 0 3px;
  background-color: #516172;
  z-index: 4;
}
.header--companions {
  background-color: #182044;
}
@media (max-width: 1023px) {
  .header {
    padding: 41px 0;
    z-index: 9;
  }
  .header--companions {
    padding: 45px 0;
  }
}
@media (max-width: 767px) {
  .header {
    padding: 35px 0 44px;
  }
  .header--companions {
    padding: 18px 0;
  }
}

.header__container {
  display: flex;
  gap: 38px;
}
@media (max-width: 1023px) {
  .header__container {
    display: grid;
    grid-template-columns: auto auto;
    justify-content: space-between;
    align-items: center;
  }
}

.header__wrapper {
  display: flex;
  gap: 40px;
  width: 100%;
}
.header__wrapper::before {
  display: block;
  min-width: 60px;
  height: 2px;
  margin: 28px 0 0;
  background-color: #ffffff;
  content: "";
}
@media (max-width: 1023px) {
  .header__wrapper {
    justify-content: center;
  }
  .header__wrapper::before {
    display: none;
  }
}

@media (max-width: 1023px) {
  .header__wrapper:has(a:focus-visible) .header__burger::after {
    transform: translateY(-3px) rotate(45deg);
    background-color: #cccccc;
  }
  .header__wrapper:has(a:focus-visible) .header__burger::before {
    transform: translateY(5px) rotate(-45deg);
    background-color: #cccccc;
  }
  .header__wrapper:has(a:focus-visible) .header__burger ~ .nav {
    transform: translateY(100%);
  }
  .header__wrapper:has(a:focus-visible) .header__burger ~ .nav > .nav__collapse-wrapper {
    grid-template-rows: 1fr;
  }
}

@media (max-width: 767px) {
  .header__burger {
    margin: -15px -11px;
  }
}

.header__logo {
  color: #ffffff;
  justify-self: flex-start;
  transition: color 0.3s ease;
}
@media (max-width: 1023px) {
  .header__logo {
    z-index: 2;
  }
}

.header:has(.burger.is-active) .header__logo {
  color: #192144;
}

.nav {
  width: 100%;
  color: #ffffff;
}
@media (max-width: 1023px) {
  .nav {
    position: absolute;
    top: -100%;
