    gap: 14px;
    font-weight: 500;
    font-size: 15px;
    line-height: 107%;
  }
}

.card__item-flag {
  order: -1;
}

.card__item-flag img {
  width: 35px;
  height: 24px;
}
@media (max-width: 1023px) {
  .card__item-flag img {
    width: 40px;
  }
}
@media (max-width: 767px) {
  .card__item-flag img {
    width: 26px;
    height: 18px;
  }
}

.card__wrapper {
  grid-row: 2/span 1;
  grid-column: 2/span 1;
  align-self: flex-end;
  display: grid;
  grid-template-columns: 1fr auto;
  justify-self: flex-end;
  align-items: center;
  gap: 27px;
  width: 100%;
}
@media (max-width: 1279px) {
  .card__wrapper {
    grid-row: 1/span 2;
    grid-column: 4/span 1;
    grid-template-columns: auto;
    justify-items: center;
    align-items: flex-start;
    align-content: flex-start;
    gap: 11px;
  }
}
@media (max-width: 767px) {
  .card__wrapper {
    grid-row: 5/span 1;
    grid-column: 1/span 4;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    margin: 0;
    padding: 16px 21px 13px;
    border-top: 1px solid #edeff6;
  }
}

.card__list-transport {
  display: flex;
  flex-wrap: wrap;
  gap: 7px;
  margin: 0;
  padding: 0;
  list-style: none;
}
.card__list-transport:not(:has(.card__item-transport))::after {
  display: none;
}
@media (max-width: 1279px) {
  .card__list-transport {
    display: grid;
    gap: 6px;
    margin: 3px 3px 0 8px;
  }
}
@media (max-width: 767px) {
  .card__list-transport {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    gap: 1px;
    align-items: center;
    height: 100%;
    margin: 0;
  }
  .card__list-transport::after {
    position: absolute;
    top: -13px;
    left: -8px;
    transform: translateY(-100%);
    padding: 0 3px 0 12px;
    font-weight: 400;
    font-size: 14px;
    line-height: 135%;
    color: #444444;
    background-color: #ffffff;
    content: ":";
  }
}

.card__item-transport {
  display: grid;
  color: rgba(29, 46, 91, 0.15);
}
@media (max-width: 767px) {
  .card__item-transport {
    place-content: center;
    width: 32px;
    height: 32px;
    background-color: #edeff6;
    border-radius: 50%;
  }
  .card__item-transport > svg {
    width: 14px;
    height: 16px;
  }
}

.card__item-transport.is-active {
  color: #1d2e5b;
}

.card__button-wrapper {
  grid-row: 2/span 1;
  grid-column: 1/span 1;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  align-self: flex-end;
  gap: 4px;
  padding: 0 0 8px 8px;
}
@media (max-width: 1279px) {
  .card__button-wrapper {
    grid-row: 2/span 1;
    grid-column: 3/span 1;
    align-self: flex-end;
    padding: 0 0 6px;
  }
}
@media (max-width: 767px) {
  .card__button-wrapper {
    display: contents;
  }
}

.card__button {
  min-width: 150px;
  min-height: 50px;
  padding: 12px 18px 9px 15px;
  font-weight: 700;
  font-size: 20px;
  line-height: 100%;
  text-transform: uppercase;
  color: #161c35;
  background-color: #a8d2f4;
  border-radius: 25px;
  border: none;
  cursor: pointer;
  user-select: none;
  transition: background-color 0.3s ease, color 0.3s ease;
}
@media (hover: hover) {
  .card__button:hover:not(.focus-visible) {
    background-color: #4d99d6;
  }
  .card__button:hover:not(.focus-visible):active {
    color: rgba(22, 28, 53, 0.3);
  }
}
.card__button.focus-visible:focus {
  background-color: #4d99d6;
}
.card__button.focus-visible:focus:active {
  color: rgba(22, 28, 53, 0.3);
}
@media (max-width: 1279px) {
  .card__button {
    min-width: 170px;
  }
}
@media (max-width: 767px) {
  .card__button {
    grid-row: 6/span 1;
    grid-column: 2/span 2;
    min-height: 40px;
    font-family: var(--font-family);
    font-size: 17px;
  }
}

.card__checkbox-like {
  display: flex;
  align-items: center;
  gap: 3px;
  font-weight: 700;
  font-size: 20px;
  line-height: 100%;
  text-transform: uppercase;
  user-select: none;
  cursor: pointer;
}
@media (max-width: 767px) {
  .card__checkbox-like {
    grid-row: 2/span 1;
    grid-column: 2/span 2;
    align-items: baseline;
    gap: 7px;
    font-size: 14px;
    line-height: 114%;
    margin: 0 0 13px 47px;
  }
}

.card__checkbox-icon {
  position: relative;
  display: grid;
  place-content: center;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: transparent;
  stroke: rgba(25, 33, 68, 0.3);
  fill: transparent;
  order: -1;
  transition: background-color 0.3s ease, stroke 0.3s ease, fill 0.3s ease;
}
.card__checkbox-icon:hover, .card__checkbox-icon:focus-visible {
  background-color: #e2e5f2;
  stroke: rgba(25, 33, 68, 0.5);
}
.card__checkbox-icon:active {
  stroke: rgba(25, 33, 68, 0.1);
}
@media (max-width: 1279px) {
  .card__checkbox-icon {
    width: 57px;
  }
  .card__checkbox-icon > svg {
    width: 23px;
    height: 21px;
  }
}
@media (max-width: 767px) {
  .card__checkbox-icon {
    width: 30px;
    height: 30px;
    background-color: #e2e5f2;
  }
  .card__checkbox-icon > svg {
    width: 12px;
    height: 12px;
  }
}

.card__checkbox-like:has(input:focus-visible) .card__checkbox-icon {
  background-color: #e2e5f2;
  stroke: rgba(25, 33, 68, 0.5);
  outline: 1px solid rgba(25, 33, 68, 0.5);
}

.card__checkbox-like:has(input:checked) .card__checkbox-icon {
  background-color: #f9eaea;
  stroke: transparent;
  fill: #f02323;
}
.card__checkbox-like:has(input:checked) .card__checkbox-icon:hover {
  background-color: #f9d8d8;
}
.card__checkbox-like:has(input:checked) .card__checkbox-icon:active {
  fill: rgba(240, 35, 35, 0.3);
}

.card__checkbox-like:has(input:checked):has(input:focus-visible) .card__checkbox-icon {
  background-color: #f9d8d8;
  outline: 1px solid #f02323;
}

.card__checkbox-like input {
  position: absolute;
  appearance: none;
}

.card__level {
  position: relative;
  display: grid;
  place-content: center;
  width: 64px;
  height: 64px;
  padding: 4px 0 0 5px;
}
@media (max-width: 1279px) {
  .card__level {
    margin-right: -2px;
  }
}
@media (max-width: 767px) {
  .card__level {
    width: 45px;
    height: 45px;
    margin-right: 0;
  }
  .card__level::after {
    position: absolute;
    top: -7px;
    left: 50%;
    transform: translate(-50%, -100%);
    padding: 0 10px;
    font-weight: 400;
    font-size: 14px;
    line-height: 135%;
    color: #444444;
    background-color: #ffffff;
    content: ":";
  }
}

.card__level svg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform: rotate(-85deg);
}

.card__level svg circle {
  width: 100%;
  height: 100%;
  stroke-width: 4;
  fill: none;
  stroke-linecap: round;
  stroke-dasharray: 188.4;
  stroke-dashoffset: 188.4;
  stroke: #4d99d6;
}
@media (max-width: 1023px) {
  .card__level svg circle {
    stroke: #ff8d30;
  }
}

.card__level-count {
  width: 30px;
  font-weight: 700;
  font-size: 24px;
  line-height: 100%;
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}
@media (max-width: 767px) {
  .card__level-count {
    border: none;
  }
}

.card__level-text {
  font-weight: 400;
  font-size: 14px;
  line-height: 100%;
  color: rgba(0, 0, 0, 0.4);
}
@media (max-width: 767px) {
  .card__level-text {
    display: none;
  }
}

.pagination {
  display: none;
  justify-content: space-between;
  gap: 20px;
  background-color: #ffffff;
  border-radius: 20px;
  padding: 26px 34px 24px 29px;
}
@media (max-width: 1279px) {
  .pagination {
    padding: 22px 27px 24px 28px;
  }
}
@media (max-width: 767px) {
  .pagination {
    padding: 0;
    background-color: transparent;
  }
}

.pagination__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (max-width: 767px) {
  .pagination__list {
    display: none;
  }
}

.pagination__link {
  display: grid;
  place-content: center;
  width: 50px;
  height: 50px;
  padding: 0;
  font-weight: 700;
  font-size: 20px;
  line-height: 100%;
  text-transform: uppercase;
  color: #1d2e5b;
  background-color: transparent;
  border-radius: 50%;
  border: none;
  cursor: pointer;
  user-select: none;
  transition: color 0.3s ease, background-color 0.3s ease;
}
@media (hover: hover) {
  .pagination__link:hover:not(.focus-visible) {
    color: rgba(29, 46, 91, 0.6);
  }
  .pagination__link:hover:not(.focus-visible):active {
    background-color: #4d99d6;
  }
}
.pagination__link.focus-visible:focus {
  color: rgba(29, 46, 91, 0.6);
}
.pagination__link.focus-visible:focus:active {
  background-color: #4d99d6;
}

.pagination__link.is-active {
  background-color: #4d99d6;
}

.pagination__wrapper-button {
  display: flex;
  align-items: center;
  gap: 21px;
}
@media (max-width: 1279px) {
  .pagination__wrapper-button {
    gap: 19px;
  }
}
@media (max-width: 767px) {
  .pagination__wrapper-button {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1px;
    width: 100%;
  }
}

.pagination__button {
  display: grid;
  place-content: center;
  width: 25px;
  height: 50px;
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
  user-select: none;
}
.pagination__button::after {
  display: block;
  width: 0;
  height: 0;
  border-bottom: 9px solid transparent;
  border-top: 9px solid transparent;
  border-right: 11px solid #1d2e5b;
  transition: border-right 0.3s ease;
  content: "";
}
.pagination__button--next {
  transform: rotate(180deg);
}
@media (hover: hover) {
  .pagination__button:hover:not(.focus-visible)::after {
    border-right-color: rgba(29, 46, 91, 0.6);
  }
  .pagination__button:hover:not(.focus-visible):active::after {
    border-right-color: rgba(29, 46, 91, 0.3);
  }
}
.pagination__button.focus-visible:focus::after {
  border-right-color: rgba(29, 46, 91, 0.6);
}
.pagination__button.focus-visible:focus:active::after {
  border-right-color: rgba(29, 46, 91, 0.3);
}
.pagination__button:disabled {
  opacity: 0.3;
  pointer-events: none;
}
@media (max-width: 767px) {
  .pagination__button {
    width: auto;
    height: 51px;
    background-color: #ffffff;
    border-radius: 20px 0 0 20px;
  }
}

