// Gradient

$black-to-right: linear-gradient(90deg, rgba(0, 0, 0, 0.75) 0%, rgba(0, 0, 0, 0) 75%);
$nav-bottom-border: linear-gradient(to bottom, transparent calc(100% - 20px), $color-midnight-express 20px);
// Typography
// ---------------------------------

//    html   
$fz-default: 16px;

//     ios     
// https://www.artlebedev.ru/kovodstvo/sections/159/#13
$ff-rouble: "Rouble", "Arial", sans-serif;

$ff-roboto: "Roboto", " Arial", sans-serif;
$ff-roboto-ondensed: "Roboto Condensed", "Arial", sans-serif;


// Animation
// ---------------------------------

$tf-default: ease;
$trans-default: 0.3s $tf-default;

//      $tf-default,     
$trans-modal: 0.6s cubic-bezier(0.55, 0, 0.1, 1);
$trans-nav-transform: 0.6s ease-out;
$trans-nav-grid: 0.3s ease-in-out;

$trans-600: 0.6s $tf-default;

// Viewports
// ---------------------------------

$vp-320: 320px;
$vp-375: 375px;
$vp-768: 768px;
$vp-1024: 1024px;
$vp-1280: 1280px;
$vp-1440: 1440px;
$vp-1920: 1920px;

// Retina
// ---------------------------------


$retina-dpi: 144dpi;
$retina-dppx: 1.5dppx;
